.btn {
  display: block;
  position: relative;
  background: #ca9935;
  background-image: linear-gradient(-90deg, #ffcc99 0%, #cc9933 100%);
  box-shadow: 0px 11px 16.74px 1.26px rgba(0, 0, 0, 0.27);
  width: 215px;
  height: 49px;
  border: 1px solid #ca9935;
  font-size: 18px;
  font-family: 'DIN', sans-serif;
  font-weight: 900;
  color: #000;
  z-index: 1;
  transition: all .3s linear;
}
.btn:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 140%;
  border: 1px solid #fff;
  z-index: -1;
  transition: all .3s linear;
}
.btn:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 120%;
  height: 100%;
  border: 1px solid #fff;
  z-index: -1;
  transition: all .3s linear;
}
.btn:hover {
  box-shadow: none;
}
.btn:hover:before,
.btn:hover:after {
  width: 100%;
  height: 100%;
  border: 1px solid transparent;
}
.btn-white {
  display: block;
  position: relative;
  box-shadow: 0px 11px 16.74px 1.26px rgba(0, 0, 0, 0.27);
  width: 215px;
  height: 49px;
  border: 1px solid transparent;
  margin: auto;
  font-size: 18px;
  font-family: 'DIN', sans-serif;
  font-weight: 900;
  color: #000;
  z-index: 1;
  transition: all .3s linear;
}
.btn-white:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 140%;
  border: 1px solid #cd0009;
  z-index: -1;
  transition: all .3s linear;
}
.btn-white:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 120%;
  height: 100%;
  border: 1px solid #cd0009;
  z-index: -1;
  transition: all .3s linear;
}
.btn-white:hover {
  box-shadow: none;
  background: #cd0009;
  color: #fff;
}
.btn-white:hover:before,
.btn-white:hover:after {
  width: 100%;
  height: 100%;
}
