.section__title {
  display: table;
  position: relative;
  margin: auto;
  font-size: 40px;
  font-weight: 900;
  font-family: 'DIN', sans-serif;
  color: #cd0009;
}
.section__title:before,
.section__title:after {
  content: '';
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 45px;
  height: 1px;
  background: #cd0009;
}
.section__title:before {
  left: -60px;
